.cart1{
    position: absolute;
    left: -380px;
    top: 50px;
    background-color: white;
    padding: 20px;
    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.2);
    z-index: 999;
    white-space: nowrap;
}

.cart1 h4{
    margin-bottom: 14px;
}


.cart-top img{
    width: 80px;
    height: 100px;
    object-fit: cover;
}

.cart-top{
    display: flex;
    gap: 20px;
    align-items: center;
}

.cart-top-left{
margin-bottom: 20px;
}

.total{
    display: flex;
    justify-content: space-between;
}

.cart1 button{
    background-color: #2979fe;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    padding: 5px 40px;
    color: white;
    gap: 20px;
    margin: 10px 0;
}
.reset{
    color: red;
    cursor: pointer;
}