.contact1{
    padding: 10px 400px;
    margin: 40px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #2879fe;
    color: white;
}

.input{
    outline-style: none;
    border-radius: 4px;
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
    padding: 10px;
    border: none;

}

.btn1{
    border-radius: 4px;
    border-bottom-left-radius: 0;
    border-top-left-radius: 0;
    padding: 10px;
    border: none;
    background-color: black;
    color: white;
}