.image{
    overflow: hidden;
    width: 250px;
    height: 400px;
    position: relative;
    margin-top: 40px;
}

.image img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
}

.mainImg{
    z-index: 1;
}

.image:hover .secondImg{
    z-index: 2;
}
.new{
    color: teal;
    position: absolute;
    top: 4px;
    z-index: 3;
    background-color: white;
    left: 5px;
    padding: 5px;
    border-radius: 2px;
    font-weight: bolder;
}

.price{
    display: flex;
    gap: 1rem;
}

.price h4:first-child{
    text-decoration: line-through;
}