.product{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
    height: 800px   ;
}

.product-left{
    display: grid;
    grid-template-columns: 25% 75%;
    gap: 10px;
}

.product-left img{
    width: 100%;
}

.shoppingcart button{
    background-color: #2979fe;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    padding: 5px 40px;
    color: white;
    gap: 20px;
}

.wishlist{
    display: flex;
    align-items: center;
    gap: 40px;
}

.wishlist span{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    color: #1079fe;
    margin-top: 15px;
}


.product-right{
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.quantity button{
    border: none;
    padding: 10px;
    margin: 5px;
}

.info{
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    color: gray;
}

.details{
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    color: gray;
}

.hr25{
    width: 25%;
}