.categories{
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 60px 10px;
}
.box1{
    display: flex;
    gap: 10px;
    height: 700px;
    margin-right: 5px;
}
.row1{
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 50%;
    height: 100%;
}

.row11{
    height: 100%;
    position: relative;
}
.row2{
    width: 50%;
    height: 100%;
    position: relative;
}

.categories img{
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.box2{
    display: flex;
    flex-direction: column;
    height: 700px;
    margin-left: 5px;
    gap: 10px;
}

.box3{
    display: flex;
    height: 50%;
    gap: 10px;
}
.row3{
    position: relative;
}
.row4{
    height: 50%;
    position: relative;
}

.btn{
    position: absolute;
    background: linear-gradient(to right    ,#4568DC,#B06AB3) !important;
    color: white !important;
    min-width: 100px;
    height: 50px;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: fit-content;
    margin: auto !important;
    border-radius: 6px;
    border: none;
    font-size: 0.9rem;
}
