.nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 90px;
    padding: 20px 80px;
}

.nav .nav-left{
    display: flex;
    align-items: center;
    gap: 1.3rem;
    font-size: 1.1rem;

}

.nav .nav-left img{
    object-fit: contain;
}

.nav-center{
    font-size: 1.7rem;
    letter-spacing: 2px;
}

.nav .nav-right{
    display: flex;
    align-items: center;
    gap: 1.3rem;
    font-size: 1.1rem;
}

.item{
    display: flex;
    align-items: center;
}

.icons{
    cursor: pointer;
    display: flex;
    gap: 15px;
}

.cart{
    display: flex;
    align-items: center;
    position: relative;
    cursor: pointer;
}
.cart span{
position: absolute;
color: white;
border-radius: 50%;
background-color: #2879fe;
width: 20px;
height: 20px;
display: flex;
justify-content: center;
align-items: center;
font-size: 12px;
top: -10px;
right: -10px;
}

.addcart{
    position: relative;
}