.footer{
    padding: 20px 160px;
}


.top-footer{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 1.4rem;
}

.links, .categories1{
    display: grid;
    color: gray;
}



.bottom-footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 40px;
    padding-bottom: 40px;
}


.bottom-footer img{
    width: 50%;
    object-fit: contain;
}

.footer-pay-img{
    display: flex;
    justify-content: flex-end;
}

.left-side{
    padding-top: 20px;
    display: flex;
    gap: 1rem;
    align-items: center;
    color: #2879fe;
}

.left-side span{
    font-size: 14px;
    color: gray;
}

.contact{
    text-align: justify;
    color: gray;
}
.about{
    text-align: justify;
    color: gray;
}

h3{
    color: #555;
}