.slide{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 40px;
    width: 100%;
    height: 100%;
    border-radius: 1rem;
    background-size: cover;
    background-position: center;
    transition: 500ms;
}

.slide:hover .hidden{
    display: block;
    cursor: pointer;
}

.pagination{
    display: flex;
    justify-content: center;
    color: gray;
}

.carosel{
    max-width: 1400px;
    height: 780px;
    width: 100%;
    margin: auto;
    padding: 4rem 1rem;
}